<template>
    <section>
        <h1>Hello Lookup</h1>
    </section>   
</template>

<script>
import DataService from '../../../services/DataService';

export default {
    data() {
        return {
            triggerId: '',
            triggerTitle:'',
            SortExp: '',
            SortBy: '',
            PageNumber: 1,
            PageSize: 10,
            triggerlink:'',
            logid: ''
        }
    },
    async created() { 
        var vm = this;
        vm.logid = 1;
        //vm.triggerId = 7;
        vm.triggerId = vm.$route.params._id;
        var type = vm.$route.params.type;
        await vm.GetTriggerLinkListing();
        if(type = 'is_read'){
           await vm.GetRead();
        }else if(type = 'is_clicked'){
           await vm.GetClicked();
           await vm.SaveTriggerLinkCount();
        }else if(type = 'subscribe'){
            await vm.GetSubscribed();
        }
    },
    methods: {
        GetRead: async function () {
           var vm = this;
           var url = `_id=${vm.logid}`;
           await DataService.GetRead(url).then(response=>{
            debugger
           });
        },
        GetClicked: async function () {
            var vm = this;
            var url = `_id=${vm.logid}`;
            await DataService.GetClicked(url).then(response => {
                debugger
            })
        },
        GetSubscribed: async function () {
            var vm = this;
            var url = `_id=${vm.logid}`;
            await DataService.GetSubscribed(url).then(response => {
                debugger
            })
        },
        SaveTriggerLinkCount: async function () {
            var vm = this;
            var url = `_id=${vm.triggerId}`;
            await DataService.SaveTriggerLinkCount(url).then(response => {
                debugger
            })
        },
        GetTriggerLinkListing: async function () {
            try {
                var vm = this;
                var url = `triggerId=${vm.triggerId}&triggerTitle=${vm.triggerTitle}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
                const response = await DataService.GetTriggerLinkListing(url);
                if (response.data.data != '') {
                    vm.triggerlink = response.data.data[0].TRIGGER_LINK;
                    window.location.href = vm.triggerlink; // Redirect to trigger link
                }
            } catch (error) {
                console.error("Error occurred while fetching trigger link:", error);
            }
        }

        // GetTriggerLinkListing: async function () {
        //     var vm = this;
        //     var url = `triggerId=${vm.triggerId}&triggerTitle=${vm.triggerTitle}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
        //     await DataService.GetTriggerLinkListing(url).then(response => {
        //         if(response.data.data != ''){
        //             debugger
        //             vm.triggerlink = response.data.data[0].TRIGGER_LINK;
        //         }
        //     })
        // }
    }
}
</script>


